const isAsync = true;

/**
 * Parse CSV line respecting quotes
 * @param {string} line - CSV line
 * @returns {string[]} Parsed values
 */
const parseCSVLine = line => {
  const result = [];
  let current = '';
  let inQuotes = false;
  for (let i = 0; i < line.length; i++) {
    const char = line[i];
    if (char === '"') {
      if (inQuotes && line[i + 1] === '"') {
        current += '"';
        i++;
      } else {
        inQuotes = !inQuotes;
      }
    } else if (char === ',' && !inQuotes) {
      result.push(current.trim());
      current = '';
    } else {
      current += char;
    }
  }
  result.push(current.trim());
  return result;
};

// Analyze CSV columns and check against existing data elements
const analyzeCSVForDataElements = async (fileContent, baseUrl) => {
  console.log('=== Event Import: Analyzing CSV columns ===');
  const lines = fileContent.split('\n').filter(line => line.trim());
  if (lines.length < 2) {
    return {
      dataElementColumns: [],
      missingDataElements: [],
      existingDataElements: {}
    };
  }
  const headers = parseCSVLine(lines[0]);
  console.log('CSV Headers:', headers);

  // All columns except DATE are potential data elements
  const dataElementColumns = headers.filter(h => h.trim().toUpperCase() !== 'DATE');
  console.log('Data element columns:', dataElementColumns);

  // Pre-scan to detect value types
  const parsedLines = lines.slice(1).filter(l => l.trim()).map(l => parseCSVLine(l));
  const columnValueTypes = {};
  dataElementColumns.forEach(header => {
    const colIdx = headers.indexOf(header);
    const hasText = parsedLines.some(vals => {
      const v = (vals[colIdx] || '').trim();
      return v !== '' && isNaN(v);
    });
    columnValueTypes[header] = hasText ? 'TEXT' : 'NUMBER';
  });

  // Fetch existing data elements
  const deUrl = `${baseUrl}/api/dataElements.json?fields=id,displayName,name,valueType,aggregationType,categoryCombo[id,categoryOptionCombos[id]]&paging=false`;
  let dataElementsList = [];
  try {
    const resp = await fetch(deUrl, {
      credentials: 'include'
    });
    const data = await resp.json();
    dataElementsList = data.dataElements || [];
    console.log('Fetched data elements count:', dataElementsList.length);
  } catch (e) {
    console.error('Failed to fetch data elements:', e);
    dataElementsList = [];
  }

  // Build lookup map
  const existingDataElements = {};
  dataElementsList.forEach(de => {
    const displayName = de.displayName || de.name;
    existingDataElements[displayName] = de;
    existingDataElements[displayName.toLowerCase()] = de;
    existingDataElements[de.name] = de;
    existingDataElements[de.name.toLowerCase()] = de;
  });

  // Find missing data elements
  const missingDataElements = dataElementColumns.filter(h => !existingDataElements[h] && !existingDataElements[h.toLowerCase()]).map(name => ({
    name,
    valueType: columnValueTypes[name]
  }));
  console.log('Missing data elements:', missingDataElements);
  return {
    dataElementColumns,
    missingDataElements,
    existingDataElements,
    columnValueTypes
  };
};

